import React from 'react';

let myEventsList = [
  {
  'date': '9/16/17',
  'time': '5:00pm',
  'eventTitle': 'Homecomming',
},
{
  'date': '9/17/17',
  'time': '7:00pm',
  'eventTitle': 'Homecomming2',
}
];

const Events = (props) => {
  return (
    <div>
      {
        myEventsList.length ? (
          myEventsList.map((event) => {
            return (
              <div>
                {event.date} {event.time} {event.eventTitle}
              </div>
            )
          })
        ) : null
      }
    </div>
  );
};

export default Events;



